@import "variables";
html {
  @include box-sizing(border-box);
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
  scroll-margin-top: 100px;
}

*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: $fallback;
  background-color: $bgcolor;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  margin: 0;
}

h1,
h2 {
  font-family: $fontHeader;
  padding: 0;
  margin: 0;
  color: $textcolor1;
}

p {
  font-family: $fontText;
  line-height: 1.7em;
  margin: 0;
  margin-bottom: 0.5em;
  color: $textcolor1;
}

a {
  text-decoration: none;
  font-family: $fontText;
  color: $textcolor1;
}

/* unvisited link */
a:link {
  color: $textcolor1;
}

/* visited link */
a:visited {
  color: $textcolor1;
  border: $textcolor1 dotted 1px;
}

a:hover {
  cursor: pointer;
}

/* selected link */
a:active {
  color: $textcolor1;
  border: $textcolor1 dotted 1px;
}

hr {
  margin: 1em auto;
  width: 70%;
  display: block;
  color: $textcolor1;
}

